/* Header menu */
// Layout was inlined
.js .header__inline-menu {
  @media screen and (max-width: 799px) {
    position: absolute;
    right: 0;
    left: 0;
    width: 100%;;
    z-index: 999;
    top: -102vh;
    // height: calc(100vh - 56px);
    height: 100vh;
    transition: var(--transition);
    &.active {
      transform: translateY(102vh);
      overflow: scroll;
    }
  }
  @media screen and (min-width: 800px) {
  }
}


.list-menu {
  list-style: none;
  padding: 0;
  margin: 0;
}

.list-menu--inline {
  flex-direction: column;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  width: 100%;
  align-items: center;
  padding: 140px 24px 16px 24px;
  @media screen and (min-width: 800px) {
    flex-direction: row;
    padding: 0;
    display: inline-flex;
    flex-wrap: wrap;
  }
}

.list-menu__item {
  display: flex;
  align-items: center;
  line-height: 1.3;
}

.list-menu__item--link {
  text-decoration: none;
  padding-bottom: 1rem;
  padding-top: 1rem;
  line-height: 1.8;
}

@media screen and (min-width: 750px) {
  .list-menu__item--link {
    padding-bottom: 0.5rem;
    padding-top: 0.5rem;
  }
}

.header__inline-menu {
  font-family: var(--font-heading-family);
  ul {
    li {
      @media screen and (max-width: 799px) {
        display: block;
        width: 100%;
        position: relative;
        &:nth-child(1) {
          z-index: 1;
        }
        &:nth-child(2) {
          z-index: 2;
        }
        &:nth-child(3) {
          z-index: 3;
        }
        &:nth-child(4) {
          z-index: 4;
        }
        &:nth-child(5) {
          z-index: 5;
        }
        &:nth-child(6) {
          z-index: 6;
        }
        &:nth-child(7) {
          z-index: 7;
        }
        &:nth-child(8) {
          z-index: 8;
        }
        &:nth-child(9) {
          z-index: 9;
        }
        &:nth-child(10) {
          z-index: 10;
        }
        &:nth-child(11) {
          z-index: 11;
        }
        &:nth-child(12) {
          z-index: 12;
        }
        &:nth-child(13) {
          z-index: 13;
        }
      }
      svg {
        width: 16px;
        position: absolute;
        right: 20px;
        top: 24px;
        z-index: 4;
        @media screen and (max-width: 799px) {
          top: 20px;
        }
        path {
          fill: var(--color-dark);
        }
      }
      &.active {
        svg {
          transform: rotate(180deg);
        }
      }
      &.subnav_active {
        cursor: pointer;
        @media screen and (min-width: 800px) {
          z-index: 1;
          &.active {
            z-index: 2;
          }
        }
        span {
          padding-right: 40px;
          position: relative;
        }
        @media screen and (max-width: 799px) {
          display: block;
        }
        &.active {
          span {
            color: var(--color-foreground); 
            svg path {
              fill: var(--color-foreground); 
            }
            &:after {
              background-color: var(--color-foreground); 
            }
          }
        } 
      }
    }
  }
}

// .header--top-center .header__inline-menu,
