.header__submenu {
  width: 100%;
  opacity: 0;
  z-index: 1;
  .inner {
    padding-top: 20px;
    padding-bottom: 20px;
  }
  @media screen and (max-width: 799px) {
    max-height: 0;
    padding-top: 0;
    background-color: white;
  }
  @media screen and (min-width: 800px) {
    position: absolute;
    left: 0;
    right: 0;
    max-height: 0;
    overflow: hidden;
    transition: all 0.4s cubic-bezier(0.32, 0.24, 0.15, 1);
    background-color: rgba(255,255,255,0.0);
    .inner {
      opacity: 0;
      transition: opacity 0.2s cubic-bezier(0.32, 0.24, 0.15, 1);
      @media screen and (min-width: 800px) {
        flex-wrap: nowrap;
        justify-content: space-between;
      }
    }
  }
  .flex {
    .list-menu {
      flex: 1;
      gap: 8px;
      align-content: center;
      // max-width: 520px;
      @media screen and (max-width: 800px) {
        &.has-2, &.has-1 {
          flex: 1 0 100%;
        }
      }
      @media screen and (min-width: 800px) {
        flex: 1;
        &.has-4 {
          flex-wrap: nowrap;
        }
      }
      li {
        flex: 1 0 100%;
        padding: 8px 8px 8px 0;
        @media screen and (min-width: 800px) {
          padding: 8px 8px;
        }
        a {
          color: var(--color-foreground);
          padding: 4px 4px 4px 0;
        }
        .larger_link {
          @media screen and (min-width: 800px) {
            font-size: 112.5%;
          }
        }
        &.sub_subnav {
          flex: 1 1 25%;
          justify-content: flex-start;
          flex-wrap: nowrap;
          gap: 0;
          .subnav_menu {
            padding: 0;
            margin: 0;
            list-style-type: none;
            flex: 1 0 63%;
            @media screen and (min-width: 800px) {
              padding: 0 0 8px 0;
            }
          }
          .subnav_title {
            flex: 0 1 auto;
            color: var(--color-menu);
            // color: #760a12;
            padding: 9px 0 10px 0;
            .nolink {
              padding-right: 4px;
              color: #777;
            }
            @media screen and (min-width: 800px) {
              padding: 9px 0 0 0;
            }
            // &:after {
            //   content: " -";
            // }
          }
        }
      }
    }
    .creative {
      flex: 1;
      padding-bottom: 20px;
      position: relative;
      text-align: center;
      color: rgba(var(--color-red-rgb),0.7);
      @media screen and (min-width: 800px) {
        flex: 0 1 22%;
        padding-top: 20px;
      }
      strong {
        display: block;
        font-size: 112.5;
        color: var(--color-foreground);
      }
        .navshot {
          max-width: 200px;
          @media screen and (min-width: 768px) {
            max-width: 250px;
        }
      }
      > a {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 2;
        display: block;
      }
    }
  }
}
.nolink {
  cursor: default;
}
// Set up litle animations for the menu dropdown lists
.header__submenu .list-menu {
  > li {
    opacity: 0;
    transform: translateX(-8px);
    transition: all 0.2s cubic-bezier(0.32, 0.24, 0.15, 1);
    &:nth-child(1) {
      transition-delay: 0.05s;
    }
    &:nth-child(2) {
      transition-delay: 0.1s;
    }
    &:nth-child(3) {
      transition-delay: 0.15s;
    }
    &:nth-child(4) {
      transition-delay: 0.2s;
    }
    &:nth-child(5) {
      transition-delay: 0.25s;
    }
    &:nth-child(6) {
      transition-delay: 0.3s;
    }
    &:nth-child(7) {
      transition-delay: 0.35s;
    }
    &:nth-child(8) {
      transition-delay: 0.4s;
    }
    &:nth-child(9) {
      transition-delay: 0.45s;
    }
    &:nth-child(10) {
      transition-delay: 0.5s;
    }
  }        
}

.header__submenu {
  .creative {
    opacity: 0;
    transform: translateX(-8px);
    transition: all 0.2s cubic-bezier(0.32, 0.24, 0.15, 1);
    &:nth-child(1) {
      transition-delay: 0.15s;
    }
    &:nth-child(2) {
      transition-delay: 0.2s;
    }
    &:nth-child(3) {
      transition-delay: 0.25s;
    }
    &:nth-child(4) {
      transition-delay: 0.3s;
    }
  } 
}


.subnav_active {
  &.active {
    .header__submenu {
        max-height:  700px;
        opacity: 1;
    }
  }
  &.showBg {
    .header__submenu {
      @media screen and (min-width: 800px) {
        background-color: white;
      }
    }
  }
  &.showCt {
    .header__submenu {
        .inner {
          opacity: 1;
        }
        .list-menu {
          > li {
            opacity: 1;
            transform: translateX(0);
          }
        }
        .creative {
        opacity: 1;
        transform: translateX(0);
      }
    }
  }
}



.header__menu-item {
  padding: 16px;
  text-decoration: none;
  color: var(--color-menu);
  font-family: var(--font-heading-family);
  background-color: white;
  position: relative;
  z-index: 3;
  @media screen and (max-width: 799px) {
    text-align: left;
    display: block;
  }
}
.list-menu > li > .header__menu-item {
  height: 60px;
  display: block;
}

.header__menu-item {
  transition: color var(--duration-short) ease;
}

.header__menu-item:hover {
  color: var(--color-foreground);
  svg path {
    fill: var(--color-foreground); 
  }
}

.header__active-menu-item {
  color: var(--color-menu);
  position: relative;
  &:after {
    content: " ";
    display: block;
    position: absolute;
    height: 1px;
    bottom: 16px;
    left: 15px;
    width: 20px;
    background-color: var(--color-menu);
    transition: all var(--duration-default) ease;
    @media screen and (min-width: 800px) {
      width: 30%;
      bottom: 8px;
    }
  }
  &:hover {
    color: var(--color-foreground);
    svg path {
      fill: var(--color-foreground); 
    }
    &:after {
      width: 30px;
      background-color: var(--color-foreground);
      @media screen and (min-width: 800px) {
        width: 60%;
      }
    }
  }
}

button.navcloser.nude {
  display: none;
  @media screen and (min-width: 800px) {
    display: block;
    position: fixed;
    padding: 0;
    z-index: -1;
    width: 0px;
    height: 0px;
    bottom: 0px;
    // background-color: var(--color-blue);
    &.active {
      width: 100%;
      left: 0;
      right: 0;
      height: 100vh;
      z-index: 1;
      top: 0;
      .shopify-section-header-hidden & {
        height: calc(100vh + 124px);
      }
    }
  }
}

// Burgers
@charset "UTF-8";
/*!
 * Hamburgers
 * @description Tasty CSS-animated hamburgers
 * @author Jonathan Suh @jonsuh
 * @site https://jonsuh.com/hamburgers
 * @link https://github.com/jonsuh/hamburgers
 */

// Settings
// ==================================================
$hamburger-padding-x           : 15px !default;
$hamburger-padding-y           : 15px !default;
$hamburger-layer-width         : 40px !default;
$hamburger-layer-height        : 3px !default;
$hamburger-layer-spacing       : 8px !default;
$hamburger-layer-color         : #fff !default;
$hamburger-layer-border-radius : 0px !default;
$hamburger-hover-opacity       : 0.7 !default;
$hamburger-active-layer-color  : var(--color-burger) !default;
$hamburger-active-hover-opacity: $hamburger-hover-opacity !default;

// To use CSS filters as the hover effect instead of opacity,
// set $hamburger-hover-use-filter as true and
// change the value of $hamburger-hover-filter accordingly.
$hamburger-hover-use-filter   : false !default;
$hamburger-hover-filter       : opacity(50%) !default;
$hamburger-active-hover-filter: $hamburger-hover-filter !default;


.template-page, .template-product, .template-collection, .template-cart {
  --color-burger: var(--color-dark);
}
.white_logo {
  --color-burger: white;
}
// Types (Remove or comment out what you don’t need)
// ==================================================
$hamburger-types: (
  spring
) !default;

// Hamburger
// ==================================================
.nude.hamburger {
  @media screen and (min-width:800px) {
    display: none;
    visibility: hidden;
  }
  position: absolute;
  z-index: 9999;
  right: 20px;
  top: calc(100% + 16px);
  padding: $hamburger-padding-y $hamburger-padding-x;
  display: inline-block;
  cursor: pointer;
  min-width: 40px;
  transition-property: opacity, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;

  // Normalize (<button>)
  font: inherit;
  color: inherit;
  text-transform: none;
  background-color: transparent;
  border: 0;
  margin: 0;
  overflow: visible;

  &:hover {
    @if $hamburger-hover-use-filter == true {
      filter: $hamburger-hover-filter;
    }
    @else {
      opacity: $hamburger-hover-opacity;
    }
  }

  &.is-active {
    &:hover {
      @if $hamburger-hover-use-filter == true {
        filter: $hamburger-active-hover-filter;
      }
      @else {
        opacity: $hamburger-active-hover-opacity;
      }
    }

    .hamburger-inner,
    .hamburger-inner::before,
    .hamburger-inner::after {
      background-color: var(--color-dark);
    }
  }
}

.hamburger-box {
  width: $hamburger-layer-width;
  height: $hamburger-layer-height * 3 + $hamburger-layer-spacing * 2;
  display: inline-block;
  position: relative;
}

.hamburger-inner {
  display: block;
  top: 50%;
  margin-top: $hamburger-layer-height / -2;

  &,
  &::before,
  &::after {
    width: $hamburger-layer-width;
    height: $hamburger-layer-height;
    background-color: var(--color-burger);
    border-radius: $hamburger-layer-border-radius;
    position: absolute;
    transition-property: transform;
    transition-duration: 0.15s;
    transition-timing-function: ease;
  }

  &::before,
  &::after {
    content: "";
    display: block;
  }

  &::before {
    top: ($hamburger-layer-spacing + $hamburger-layer-height) * -1;
  }

  &::after {
    bottom: ($hamburger-layer-spacing + $hamburger-layer-height) * -1;
  }
}

// Hamburger types
// ==================================================

@if index($hamburger-types, spring) {
  /*
   * Spring
   */
  .hamburger--spring {
    .hamburger-inner {
      top: $hamburger-layer-height / 2;
      transition: background-color 0s 0.13s linear;

      &::before {
        top: $hamburger-layer-height + $hamburger-layer-spacing;
        transition: top 0.1s 0.2s cubic-bezier(0.33333, 0.66667, 0.66667, 1),
                    transform 0.13s cubic-bezier(0.55, 0.055, 0.675, 0.19);
      }

      &::after {
        top: ($hamburger-layer-height * 2) + ($hamburger-layer-spacing * 2);
        transition: top 0.2s 0.2s cubic-bezier(0.33333, 0.66667, 0.66667, 1),
                    transform 0.13s cubic-bezier(0.55, 0.055, 0.675, 0.19);
      }
    }

    &.is-active {
      .hamburger-inner {
        transition-delay: 0.22s;
        background-color: transparent !important;

        &::before {
          top: 0;
          transition: top 0.1s 0.15s cubic-bezier(0.33333, 0, 0.66667, 0.33333),
                      transform 0.13s 0.22s cubic-bezier(0.215, 0.61, 0.355, 1);
          transform: translate3d(0, $hamburger-layer-spacing + $hamburger-layer-height, 0) rotate(45deg);
          background-color: var(--color-dark);
        }

        &::after {
          top: 0;
          transition: top 0.2s cubic-bezier(0.33333, 0, 0.66667, 0.33333),
                      transform 0.13s 0.22s cubic-bezier(0.215, 0.61, 0.355, 1);
          transform: translate3d(0, $hamburger-layer-spacing + $hamburger-layer-height, 0) rotate(-45deg);
          background-color: var(--color-dark); 
        }
      }
    }
  }
}


// ==================================================
// Cooking up additional types:
//
// The Sass for each hamburger type should be nested
// inside an @if directive to check whether or not
// it exists in $hamburger-types so only the CSS for
// included types are generated.
//
// e.g. hamburgers/types/_new-type.scss
//
// @if index($hamburger-types, new-type) {
//   .hamburger--new-type {
//     ...
//   }
// }

